.portfolio-box {
	display: flex;
	flex-direction: row;
	justify-content: center;
	flex-wrap: wrap;
	gap: 1rem;
    
}

.portfolio-link-group {
	text-decoration: none;
}

.portfolio-container {
	width: 100%;
}

.portfolio-card {
	background-color: var(--card-blue-shade);
	color: var(--nav-grey);
	border-radius: 5px;
	position: relative;
	padding: 1rem 2rem;
	width: 300px;
	max-width: 300px;
	margin: 0 auto;
	height: 250px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	cursor: pointer;
	transition: all 0.3s ease;
}

.portfolio-card:hover {
	transform: translateY(-7px);
}

.portfolio-card:hover .portfolio-title {
	color: var(--nav-blue);
}

.top-portfolio {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 2rem;
}

.top-portfolio .right {
	display: flex;
}

.mid-portfolio {
	margin-bottom: 2rem;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.portfolio-title {
	font-size: 1.3rem;
	font-weight: 700;
	word-spacing: 5px;
	margin-bottom: 0.5rem;
}

.portfolio-desc {
	font-size: 70%;
}

.portfolio-folder {
	font-size: 2rem;
	color: var(--nav-blue);
}

.portfolio-git,
.portfolio-link {
	color: var(--nav-grey);
	margin: 0 0.5rem;
	font-size: 1.5rem;
	display: flex;
	align-items: center;
	transition: all 0.3s ease;
}
.portfolio-git:hover,
.portfolio-link:hover {
	color: var(--nav-blue);
}

.bottom-portfolio {
	display: flex;
	flex-wrap: wrap;
	gap: 5px;
	justify-content: center;
	align-items: center;
}

.bottom-portfolio small {
	margin: 0 0.5rem;
	font-size: 60%;
}

/* Media Queries */
@media screen and (max-width: 750px) {
}

@media screen and (max-width: 550px) {
	.portfolio-desc {
		font-size: 60%;
	}

	.portfolio-folder {
		font-size: 1.5rem;
	}

	.portfolio-git,
	.portfolio-link {
		font-size: 1.2rem;
	}

	.portfolio-title {
		font-size: 1.2rem;
	}

	.bottom-portfolio small {
		font-size: 50%;
	}
}

@media screen and (max-width: 320px) {
	.portfolio-card {
		width: 250px;
		max-width: 300px;
		margin: 0 auto;
		height: 250px;
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		cursor: pointer;
		transition: all 0.3s ease;
	}
}
